/* Paleta de cores */
:root {
  --verde: #2e7d32;
  --verde-claro: #4caf50;
  --branco: #fff;
  --cinza-claro: #f5f5f5;
}

.page {
  font-family: 'Segoe UI', Arial, sans-serif;
  background: var(--cinza-claro);
  color: var(--verde);
}

/******** Animações Gerais ********/
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.page > section:not(.destaque), .page > footer {
  animation: fadeInUp 0.8s cubic-bezier(.4,0,.2,1);
  opacity: 0;
  transform: translateY(40px);
  transition: opacity 0.7s cubic-bezier(.4,0,.2,1), transform 0.7s cubic-bezier(.4,0,.2,1);
}
.show {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

/******** Banner ********/
.banner {
  position: relative;
  min-height: 340px;
  background: url('/banner-placeholder.jpg') center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bannerOverlay {
  background: rgba(46, 125, 50, 0.7);
  width: 100%;
  padding: 60px 20px 50px 20px;
  text-align: center;
  color: #fff;
  border-radius: 0 0 24px 24px;
  animation: fadeIn 1.2s cubic-bezier(.4,0,.2,1);
}
.bannerOverlay h1 {
  font-size: 2.8rem;
  margin-bottom: 10px;
  letter-spacing: 2px;
}
.bannerOverlay p {
  font-size: 1.3rem;
  margin-bottom: 24px;
}
.bannerBtn {
  background: #fff;
  color: #2e7d32;
  border: none;
  border-radius: 24px;
  padding: 12px 32px;
  font-size: 1.1rem;
  font-weight: bold;
  text-decoration: none;
  box-shadow: 0 2px 8px rgba(46,125,50,0.10);
  transition: background 0.2s, color 0.2s, box-shadow 0.2s, transform 0.2s;
}
.bannerBtn:hover {
  background: #4caf50;
  color: #fff;
}

/******** Serviços ********/
.servicos {
  background: #fff;
  margin: -40px auto 32px auto;
  max-width: 1000px;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(46,125,50,0.10);
  padding: 32px 20px 16px 20px;
  position: relative;
  z-index: 2;
}
.servicosGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  justify-content: center;
}
.servicoCard {
  background: #f5f5f5;
  border-radius: 12px;
  box-shadow: 0 1px 6px rgba(46,125,50,0.07);
  padding: 24px 18px;
  width: 200px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.25s, transform 0.25s, background 0.2s;
  cursor: pointer;
}
.servicoCard:hover {
  box-shadow: 0 6px 24px rgba(46,125,50,0.18);
  background: #e8f5e9;
  transform: translateY(-6px) scale(1.04);
}
.iconePlaceholder {
  font-size: 2.2rem;
  margin-bottom: 10px;
}

/******** Sobre ********/
.sobre {
  background: #fff;
  margin: 32px auto;
  max-width: 1000px;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(46,125,50,0.10);
  padding: 32px 20px;
}
.sobreContent {
  display: flex;
  gap: 32px;
  align-items: center;
  flex-wrap: wrap;
}
.sobreImg {
  width: 220px;
  height: 160px;
  background: #4caf50;
  border-radius: 12px;
  opacity: 0.18;
  flex-shrink: 0;
}
.sobreContent h2 {
  color: #2e7d32;
}
.sobreBtn {
  display: inline-block;
  margin-top: 18px;
  background: #2e7d32;
  color: #fff;
  border: none;
  border-radius: 24px;
  padding: 10px 28px;
  font-size: 1rem;
  font-weight: bold;
  text-decoration: none;
  transition: background 0.2s;
}
.sobreBtn:hover {
  background: #388e3c;
}

/******** Galeria ********/
.galeria {
  background: #fff;
  margin: 32px auto;
  max-width: 1000px;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(46,125,50,0.10);
  padding: 32px 20px;
}
.galeria h2 {
  color: #2e7d32;
  margin-bottom: 18px;
}
.galeriaGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 18px;
}
.galeriaImg {
  background: #4caf50;
  border-radius: 10px;
  height: 120px;
  opacity: 0.18;
  transition: opacity 0.2s, transform 0.25s, box-shadow 0.2s;
  cursor: pointer;
}
.galeriaImg:hover {
  opacity: 0.35;
  transform: scale(1.06);
  box-shadow: 0 4px 16px rgba(46,125,50,0.15);
}

/******** Depoimentos ********/
.depoimentos {
  background: #fff;
  margin: 32px auto;
  max-width: 1000px;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(46,125,50,0.10);
  padding: 32px 20px;
}
.depoimentos h2 {
  color: #2e7d32;
  margin-bottom: 18px;
}
.depoimentosGrid {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  justify-content: center;
}
.depoimentos blockquote {
  font-style: italic;
  color: #2e7d32;
  background: #f5f5f5;
  border-left: 4px solid #4caf50;
  padding: 18px 20px;
  border-radius: 8px;
  margin: 0;
  min-width: 220px;
  max-width: 340px;
}
.depoimentos span {
  display: block;
  margin-top: 8px;
  color: #666;
  font-size: 0.95rem;
}

/******** Rodapé ********/
.footer {
  background: linear-gradient(90deg, #2e7d32 80%, #4caf50 100%);
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 32px;
  border-radius: 24px 24px 0 0;
  margin-top: 40px;
  flex-wrap: wrap;
  gap: 18px;
}
.footerSocials a {
  color: #fff;
  font-size: 2rem;
  margin-left: 12px;
  text-decoration: none;
  transition: color 0.2s;
}
.footerSocials a:hover {
  color: #c8f7c5;
}

/******** Responsividade ********/
@media (max-width: 900px) {
  .sobreContent {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer {
    flex-direction: column;
    align-items: flex-start;
    padding: 24px 12px;
  }
}
@media (max-width: 700px) {
  .servicosGrid, .depoimentosGrid {
    flex-direction: column;
    align-items: center;
  }
  .servicoCard {
    width: 100%;
    max-width: 320px;
  }
  .sobre, .servicos, .galeria, .depoimentos {
    padding: 20px 8px;
  }
  .bannerOverlay {
    padding: 40px 8px 24px 8px;
  }
}

.destaque {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.07);
  padding: 40px 48px;
  margin: 48px auto 0 auto;
  max-width: 900px;
  gap: 48px;
  min-height: 340px;
}
.destaqueColLogo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 1 1 0;
  min-width: 220px;
  max-width: 320px;
}
.destaqueLogo {
  width: 110px;
  height: 110px;
  margin-bottom: 18px;
}
.destaqueTitulo {
  font-size: 2.4rem;
  color: #7a8c5c;
  font-weight: 400;
  margin: 0 0 28px 0;
  letter-spacing: 1px;
  font-family: 'Segoe UI', Arial, sans-serif;
  line-height: 1.1;
}
.destaqueBtn {
  background: #ede7df;
  color: #7a8c5c;
  border: none;
  border-radius: 7px;
  padding: 12px 32px;
  font-size: 1.08rem;
  font-weight: 400;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(46,125,50,0.07);
  transition: background 0.2s, color 0.2s;
}
.destaqueBtn:hover {
  background: #e0d7c7;
  color: #5c6b3c;
}
.destaqueColImg {
  flex: 1 1 0;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 350px;
}
.destaqueImg {
  width: 340px;
  height: 260px;
  object-fit: cover;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(46,125,50,0.13);
  border: 4px solid #b6c3a0;
  background: #f5f5f5;
}
@media (max-width: 900px) {
  .destaque {
    flex-direction: column;
    align-items: center;
    padding: 24px 8px;
    gap: 24px;
    min-height: unset;
  }
  .destaqueColImg, .destaqueColLogo {
    align-items: center;
    width: 100%;
    max-width: 100%;
  }
  .destaqueImg {
    width: 100%;
    max-width: 340px;
    height: 180px;
  }
  .destaqueLogo {
    margin-bottom: 10px;
  }
  .destaqueTitulo {
    font-size: 1.5rem;
    margin-bottom: 18px;
  }
}
body {
  background: #f6f6f6;
}

.bgSite {
  min-height: 100vh;
  background: #f6f6f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 48px;
}
.destaqueCanva, .sobreCanva, .acomodacoesCanva, .contatoCanva, .footerCanva {
  max-width: 1200px;
}
.destaqueCanva, .sobreCanva, .acomodacoesCanva, .contatoCanva {
  padding-left: 64px;
  padding-right: 64px;
}
.destaqueCanva {
  gap: 64px;
}
.colEsq {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 1 1 0;
  min-width: 220px;
  max-width: 320px;
}
.logoCanva {
  width: 110px;
  height: 110px;
  margin-bottom: 18px;
}
.tituloCanva {
  font-size: 2.4rem;
  color: #7a8c5c;
  font-weight: 400;
  margin: 0 0 28px 0;
  letter-spacing: 1px;
  font-family: 'Segoe UI', Arial, sans-serif;
  line-height: 1.1;
}
.btnCanva {
  background: #ede7df;
  color: #7a8c5c;
  border: none;
  border-radius: 7px;
  padding: 12px 32px;
  font-size: 1.08rem;
  font-weight: 400;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(46,125,50,0.07);
  transition: background 0.2s, color 0.2s;
}
.btnCanva:hover {
  background: #e0d7c7;
  color: #5c6b3c;
}
.colDir {
  flex: 1 1 0;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 350px;
}
.imgCanva {
  width: 440px;
  height: 320px;
  max-width: 100%;
}
.sobreCanva, .acomodacoesCanva, .contatoCanva {
  margin-top: 0;
}
.sobreTitulo, .acomodacoesTitulo, .contatoTitulo {
  font-size: 1.5rem;
  color: #7a8c5c;
  font-weight: 600;
  margin-bottom: 18px;
  letter-spacing: 0.5px;
}
.sobreTexto {
  color: #444;
  font-size: 1.08rem;
  line-height: 1.7;
  margin: 0;
}
.acomodacoesTitulo {
  margin-bottom: 24px;
}
.cardsAcomodacoes {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 32px;
  justify-content: center;
  margin-top: 0;
}
.cardAcomodacao {
  background: #f6f6f6;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  padding: 24px 18px 18px 18px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 380px;
}
.cardImg {
  width: 100%;
  height: 120px;
  background: #dbe6d0;
  border-radius: 10px;
  margin-bottom: 16px;
  box-shadow: 0 1px 4px rgba(46,125,50,0.07);
}
.cardAcomodacao h3 {
  color: #7a8c5c;
  font-size: 1.1rem;
  margin: 0 0 8px 0;
  font-weight: 600;
}
.cardAcomodacao p {
  color: #444;
  font-size: 0.98rem;
  margin: 0;
}
.contatoCanva {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formCanva {
  display: flex;
  flex-direction: column;
  gap: 14px;
  width: 100%;
  max-width: 400px;
  margin-bottom: 18px;
}
.formCanva input, .formCanva textarea {
  padding: 10px;
  border: 1px solid #b6c3a0;
  border-radius: 6px;
  font-size: 1rem;
  outline: none;
  background: #f6f6f6;
  color: #444;
}
.formCanva textarea {
  min-height: 80px;
  resize: vertical;
}
.formCanva button {
  background: #7a8c5c;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 12px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background 0.2s;
}
.formCanva button:hover {
  background: #5c6b3c;
}
.contatoInfo {
  margin-top: 10px;
  color: #7a8c5c;
  font-size: 1.05rem;
  text-align: center;
}
.footerCanva {
  background: #7a8c5c;
  color: #fff;
  text-align: center;
  padding: 18px 0 12px 0;
  font-size: 1rem;
  margin-top: 40px;
  border-radius: 0 0 18px 18px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  width: 100%;
  max-width: 900px;
}
@media (max-width: 1300px) {
  .destaqueCanva, .sobreCanva, .acomodacoesCanva, .contatoCanva, .footerCanva {
    max-width: 98vw;
    padding-left: 16px;
    padding-right: 16px;
  }
  .imgCanva {
    width: 100%;
    height: auto;
    max-width: 440px;
  }
}
@media (max-width: 900px) {
  .destaqueCanva, .sobreCanva, .acomodacoesCanva, .contatoCanva {
    padding: 20px 8px 18px 8px;
  }
  .destaqueCanva {
    flex-direction: column;
    align-items: center;
    gap: 24px;
    min-height: unset;
  }
  .colDir, .colEsq {
    align-items: center;
    width: 100%;
    max-width: 100%;
  }
  .imgCanva {
    width: 100%;
    max-width: 340px;
    height: 180px;
  }
  .logoCanva {
    margin-bottom: 10px;
  }
  .tituloCanva {
    font-size: 1.5rem;
    margin-bottom: 18px;
  }
  .cardsAcomodacoes {
    grid-template-columns: 1fr;
    gap: 18px;
  }
}
